export function AppSidebar({ ...props }: React.ComponentProps<typeof Sidebar>) {
  const [user, setUser] = React.useState<any>({
    name: "shadcn",
    email: "m@example.com",
    avatar: "/avatars/shadcn.jpg",
  });

  const [account, setAccount] = React.useState<Account>();

  const getManifestr = async () => {
    const res = await DestinyService.getProfileInfo(254, 8200690);
    console.log(res);
    setUser({
      ...user,
      name:
        res.Response.bnetMembership.displayName +
        "#" +
        res.Response.bnetMembership.bungieGlobalDisplayNameCode,
    });
  };

  const getAccount = async () => {
    const res = await DestinyService.getAccountInfo(4611686018428392406);
    console.log(res);
    setAccount(res);
  };

  useEffect(() => {
    getManifestr();
    getAccount();
  }, []);
  return (
    <Sidebar collapsible="icon" {...props}>
      <SidebarHeader>
        {account && (
          <TeamSwitcher
            characters={account ? account.Response.data.characters : []}
          />
        )}
      </SidebarHeader>
      <SidebarContent>
        <NavMain items={data.navMain} />
        <NavProjects projects={data.projects} />
      </SidebarContent>
      <SidebarFooter>
        <NavUser user={user} />
      </SidebarFooter>
      <SidebarRail />
    </Sidebar>
  );
}
