import { ChevronsUpDown } from "lucide-react";

import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuShortcut,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import {
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
  useSidebar,
} from "@/components/ui/sidebar";
import { Character } from "@/interfaces/account/Account";
import { useState } from "react";

interface TeamSwitcherProps {
  characters: Character[];
}

function getCharacterClassByHash(classHash: number): string {
  switch (classHash) {
    case 3655393761:
      return "Titan"; // Hash para Titan
    case 671679327:
      return "Hunter"; // Hash para Hunter
    case 2271682572:
      return "Warlock"; // Hash para Warlock
    default:
      return "Unknown"; // Si el hash no coincide
  }
}

export function TeamSwitcher({ characters }: TeamSwitcherProps) {
  const { isMobile } = useSidebar();
  const [activeTeam, setActiveTeam] = useState<Character>(characters[0]);

  return (
    <SidebarMenu>
      <SidebarMenuItem>
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <SidebarMenuButton
              size="lg"
              className="data-[state=open]:bg-sidebar-accent data-[state=open]:text-sidebar-accent-foreground"
              style={{
                backgroundImage: `url('https://www.bungie.net/${activeTeam.backgroundPath}')`,
                backgroundSize: "cover", // Esto ajustará el fondo a la resolución del contenedor
                backgroundPosition: "center", // Centra la imagen dentro del contenedor
                color: "white", // Asegura que el texto sea legible en cualquier fondo
              }}
            >
              <div className="bg-sidebar-primary text-sidebar-primary-foreground flex aspect-square size-8 items-center justify-center rounded-lg">
                <img
                  src={`https://www.bungie.net/${activeTeam.emblemPath}`}
                  className="size-4"
                />
              </div>
              <div className="grid flex-1 text-left text-sm leading-tight">
                <span className="truncate font-semibold">
                  {getCharacterClassByHash(activeTeam.characterBase.classHash)}
                </span>
                {/* <span className="truncate text-xs">
                  {activeTeam.characterBase.characterId}
                </span> */}
              </div>
              <ChevronsUpDown className="ml-auto" />
            </SidebarMenuButton>
          </DropdownMenuTrigger>
          <DropdownMenuContent
            className="w-(--radix-dropdown-menu-trigger-width) min-w-56 rounded-lg"
            align="start"
            side={isMobile ? "bottom" : "right"}
            sideOffset={4}
          >
            <DropdownMenuLabel className="text-muted-foreground text-xs">
              Guardianes
            </DropdownMenuLabel>
            {characters.map((team, index) => (
              <DropdownMenuItem
                key={team.characterBase.classType}
                onClick={() => setActiveTeam(team)}
                className="gap-2 p-2"
                style={{
                  backgroundImage: `url('https://www.bungie.net/${team.backgroundPath}')`,
                  backgroundSize: "cover", // Esto ajustará el fondo a la resolución del contenedor
                  backgroundPosition: "center", // Centra la imagen dentro del contenedor
                  color: "white", // Asegura que el texto sea legible en cualquier fondo
                }}
              >
                <div className="flex size-6 items-center justify-center rounded-xs border">
                  <img
                    src={`https://www.bungie.net/${team.emblemPath}`}
                    className="size-4"
                  />
                </div>
                {getCharacterClassByHash(activeTeam.characterBase.classHash)}
                <DropdownMenuShortcut>⌘{index + 1}</DropdownMenuShortcut>
              </DropdownMenuItem>
            ))}
            <DropdownMenuSeparator />
          </DropdownMenuContent>
        </DropdownMenu>
      </SidebarMenuItem>
    </SidebarMenu>
  );
}
